type ParsedDateKey = 'year' | 'month' | 'date' | 'hour' | 'minute' | 'second' | 'weekday';

interface ParsedDateObject
{
	/**
	 * 
	 */
	text: string;

	/**
	 *  (0, 7: )
	 */
	value: number;
}

export type TextColorType = 'black' | 'white';

export interface ParsedDate extends Record<ParsedDateKey, ParsedDateObject>
{
	/**
	 * long time
	 */
	timestamp: number;

	/**
	 * date
	 */
	rawDate: Date;
}

export interface BaseMetadataProps
{
	/**
	 * 
	 */
	sitename?: string;

	/**
	 *  URL
	 */
	baseurl?: string;

	/**
	 * 
	 */
	title?: string;

	/**
	 * 
	 */
	description?: string;

	/**
	 * 
	 */
	keywords?: string[];

	/**
	 * 
	 */
	url?: string;

	/**
	 *  url
	 */
	thumbnail?: string;

	/**
	 *  
	 */
	subpath?: string;
}

export type ShareCallback = (data: ShareData) => void;

/**
 *      
 *
 * @param {string} hex:  HEX 
 *
 * @returns {string}
 */
export function getTextColor(hex: string): TextColorType
{
	// HEX  RGB 
	const r = parseInt(hex.slice(1, 3), 16);
	const g = parseInt(hex.slice(3, 5), 16);
	const b = parseInt(hex.slice(5, 7), 16);

	// (luminance) 
	const luminance = 0.2126 * r + 0.7152 * g + 0.0722 * b;

	//   /  
	return luminance >= 128 ? 'black' : 'white';
}

/**
 *      
 *
 * @param {number} num: 
 * @param {number} digit: 
 *
 * @returns {number}:  
 */
export function mathRound(num: number, digit?: number): number
{
	const dig = digit ?? 0;

	return Math.round(num * (10 ** dig)) / (10 ** dig);
}

/**
 *    
 *
 * @param {number} num: 
 * @param {number} size: 
 *
 * @returns {number} 
 */
export function modulo(num: number, size: number): number
{
	return ((num % size) + size) % size;
}

/**
 *   
 *
 * @param {string | number | Date} base:  
 *
 * @returns {Hour} 
 */
export function getHour(base?: string | number | Date): Hour
{
	const date = parseLocalDate(base);

	switch (date.hour.value)
	{
		case 1: return 1;

		case 2: return 2;

		case 3: return 3;

		case 4: return 4;

		case 5: return 5;

		case 6: return 6;

		case 7: return 7;

		case 8: return 8;

		case 9: return 9;

		case 10: return 10;

		case 11: return 11;

		case 12: return 12;

		case 13: return 13;

		case 14: return 14;

		case 15: return 15;

		case 16: return 16;

		case 17: return 17;

		case 18: return 18;

		case 19: return 19;

		case 20: return 20;

		case 21: return 21;

		case 22: return 22;

		case 23: return 23;

		default: return 0;
	}
}

/**
 * 0  
 *
 * @param {number} num: 
 * @param {number} digit: 
 *
 * @returns {string}  
 */
export function addZero(num: number, digit = 0): string
{
	const len = `${Math.floor(num)}`.length;
	const diff = digit - len;

	return diff > 0 ? `${'0'.repeat(diff)}${num}` : `${num}`;
}

/**
 *    
 *
 * @param {Date | number | string} source:  
 * @param {number} timezone: 
 *
 * @returns {ParsedDate}   
 */
export function parseLocalDate(source?: Date | number | string, timezone = 9): ParsedDate
{
	const baseDate = new Date(source ?? Date.now());
	const offset = -timezone - baseDate.getTimezoneOffset() / 60;

	const date = new Date(baseDate.getTime() - offset * 3600 * 1000);

	const weekday = [
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		''
	];

	const month = date.getMonth() + 1;

	return {
		date: {
			text: addZero(date.getDate(), 2),
			value: date.getDate()
		},
		hour: {
			text: addZero(date.getHours(), 2),
			value: date.getHours()
		},
		minute: {
			text: addZero(date.getMinutes(), 2),
			value: date.getMinutes()
		},
		month: {
			text: addZero(month, 2),
			value: month
		},
		rawDate: date,
		second: {
			text: addZero(date.getSeconds(), 2),
			value: date.getSeconds()
		},
		timestamp: date.getTime(),
		weekday: {
			text: weekday[date.getDay()],
			value: date.getDay()
		},
		year: {
			text: `${date.getFullYear()}`,
			value: date.getFullYear()
		}
	};
}

/**
 *     
 *
 * @param {Array} arr: 
 *
 * @returns {number}  
 */
export function pickIndex(arr: Array<unknown>): number
{
	return Math.floor(Math.random() * (arr.length - 1));
}

/**
 *   
 *
 * @param {Date | number | string} source:  
 * @param {number} dayEpoch:  
 *
 * @returns {string} 
 */
export function calcDuring(source?: Date | number | string, dayEpoch = 86400000): string
{
	const epoch = new Date(source ?? new Date())
		.getTime();
	const now = new Date()
		.getTime();

	const diff = now - epoch;

	//    
	if (diff < dayEpoch)
	{
		return ' ';
	}

	// 2  
	if (diff < dayEpoch * 15)
	{
		return `${Math.round(diff / dayEpoch)} `;
	}

	// 1  
	if (diff < dayEpoch * 365)
	{
		return `${Math.round(diff / (dayEpoch * 30))} `;
	}

	return `${Math.round(diff / (dayEpoch * 365))} `;
}

/**
 *   
 *
 * @param {string} url: URL
 */
export async function doCopy(url: string): Promise<void>
{
	await navigator.clipboard.writeText(url);
}

/**
 *  
 *
 * @param {ShareData} data:  
 * @param {ShareCallback} onSuccess:   
 * @param {ShareCallback} onError:   
 */
export async function doShare(data: ShareData, onSuccess?: ShareCallback, onError?: ShareCallback): Promise<void>
{
	//     
	if (navigator.canShare(data))
	{
		await navigator.share(data);

		onSuccess?.(data);
	}

	//  
	else
	{
		onError?.(data);
	}
}