'use client';

import { useEffect, useState } from 'react';

import { modulo, pickIndex } from '../util';

const useTypingInitOptions: UseTypingOptions = {
	stepDelay: 500,
	typingDelay: 150
};

export type UseMutationObserverCallback = (entry: MutationRecord) => void;
export type UseResizeObserverCallback = (entry: ResizeObserverEntry) => void;
export type UseIntersectionObserverCallback = (entry: IntersectionObserverEntry) => void;

export interface UseTypingOptions
{
	/**
	 *  
	 */
	typingDelay?: number;

	/**
	 *  
	 */
	stepDelay?: number;
}

/**
 * MutationObserver   
 *
 * @param {Element | string | null} ref: Element
 * @param {UseMutationObserverCallback} callback:  
 * @param {MutationObserverInit} options: 
 */
export function useMutationObserver(ref: Element | string | null, callback: UseMutationObserverCallback, options?: MutationObserverInit): void
{
	useEffect(() =>
	{
		const mo = new MutationObserver((entries) =>
		{
			entries.forEach(callback);
		});

		// DOM  
		if (ref !== null)
		{
			// ref  
			if (typeof ref === 'string')
			{
				const tag = document.querySelector(ref);

				//   
				if (tag)
				{
					mo.observe(tag, options);
				}
			}

			// DOM 
			else
			{
				mo.observe(ref, options);
			}
		}

		return (): void =>
		{
			mo.disconnect();
		};
	}, [
		ref,
		callback,
		options
	]);
}

/**
 * ResizeObserver   
 *
 * @param {Element | string | null} ref: Element
 * @param {UseResizeObserverCallback} callback:  
 * @param {ResizeObserverOptions} options: 
 */
export function useResizeObserver(ref: Element | string | null, callback: UseResizeObserverCallback, options?: ResizeObserverOptions): void
{
	useEffect(() =>
	{
		const ro = new ResizeObserver((entries) =>
		{
			entries.forEach(callback);
		});

		// DOM  
		if (ref !== null)
		{
			// ref  
			if (typeof ref === 'string')
			{
				const tag = document.querySelector(ref);

				//   
				if (tag)
				{
					ro.observe(tag, options);
				}
			}

			// DOM 
			else
			{
				ro.observe(ref, options);
			}
		}

		return (): void =>
		{
			ro.disconnect();
		};
	}, [
		ref,
		callback,
		options
	]);
}

/**
 * IntersectionObserver   
 *
 * @param {Element | string | null} ref: Element
 * @param {UseIntersectionObserverCallback} callback:  
 * @param {IntersectionObserverInit} options: 
 */
export function useIntersectionObserver(ref: Element | string | null, callback: UseIntersectionObserverCallback, options?: IntersectionObserverInit): void
{
	useEffect(() =>
	{
		const io = new IntersectionObserver((entries) =>
		{
			entries.forEach(callback);
		}, options);

		// DOM  
		if (ref !== null)
		{
			// ref  
			if (typeof ref === 'string')
			{
				const tag = document.querySelector(ref);

				//   
				if (tag)
				{
					io.observe(tag);
				}
			}

			// DOM 
			else
			{
				io.observe(ref);
			}
		}

		return (): void =>
		{
			io.disconnect();
		};
	}, [
		ref,
		callback,
		options
	]);
}

/**
 *    
 *
 * @param {(string | string[])[]} prompts: 
 * @param {UseTypingOptions} options: 
 *
 * @returns {string}  
 */
export function useTyping(prompts: (string | string[])[], options: UseTypingOptions = useTypingInitOptions): string
{
	const [
		textState,
		setTextState
	] = useState('');

	useEffect(() =>
	{
		let target = prompts[pickIndex(prompts)];
		let idx = 0;
		let forward = true;
		let interval: NodeJS.Timeout;

		const startTyping = (): void =>
		{
			interval = setInterval(() =>
			{
				//  
				if (forward)
				{
					idx++;
				}

				//   
				else
				{
					idx--;
				}

				//  ,  
				if (Array.isArray(target))
				{
					setTextState(target[idx - 1]);
				}

				//    
				else
				{
					setTextState(target.slice(0, idx));
				}

				//   ,   
				if (idx === target.length || idx === 0)
				{
					clearInterval(interval);

					setTimeout(() =>
					{
						//    ,   
						if (idx === 0)
						{
							let nextTargetIdx = pickIndex(prompts);

							//    ,   
							if (target === prompts[nextTargetIdx])
							{
								nextTargetIdx = modulo(nextTargetIdx + 1, prompts.length);
							}

							target = prompts[nextTargetIdx];
							forward = true;
						}

						//    , 
						else
						{
							forward = false;
						}

						startTyping();
					}, options.stepDelay);
				}
			}, options.typingDelay);
		};

		startTyping();

		return (): void =>
		{
			clearInterval(interval);
		};
	}, [ options ]);

	return textState;
}